a.button:hover, input.button:hover {background: #006699;color:#ffffff;}

/*-------------container */
.container{width:960px; background-color:#1E1E1E; margin:0 auto;}
/*-------------header */
header {position:relative;height: 150px;background: url(/images/img14.jpg) no-repeat center top #141414;border-bottom:1px solid #252525;}



header #search-box {position: relative;width: 300px;margin-top:30px;}
header #search-form {height: 25px;border: 1px solid #999;background-color: #fff;overflow: hidden;}
header #search-text {font-size: 14px;color: #ddd;background: transparent;}
header #search-box input[type="text"] {width: 80%;padding: 5px 0 5px 1em;color: #333;outline: none;}
header #search-button {position: absolute;top: 0;right: 0;height: 27px;width: 80px;font-size: 14px;color: #fff;text-align: center;line-height: 26px;background-color: #999;cursor: pointer;border:0;}

/*------------menu */
header nav {position:absolute;top:50px; right:50px;height: 37px;background:#000000;}
header nav ul {list-style: none;margin: 0;padding: 0;}
header nav ul li {position: relative;float: left;padding: 6px 5px 0 5px}
header nav ul li:hover {}
