header nav ul li:hover a{color:#ffffff;}
#logo-img{width: 70%;float: left;margin: 60px 40px 40px 10px;}

/*-------------content */
#content {}

.block {background:#ffffff; border-radius: 2px;}
.main-content{ padding:0 10px;}
.main-content article{overflow:hidden; margin:10px;}
.main-content .heading h2{ font-size:18px; line-height:25px; font-weight:normal; font-family: Georgia, serif; margin:10px 0;}
.main-content .heading img{float:left; margin:3px 30px 0 0; border:#000000 5px solid;}
.main-content .content{}
.main-content .footer{}
.main-content .footer .more{float:left;}
.main-content .footer .more a{ text-decoration:none; font-style:italic;}

.main-content .post{}
.main-content .post .heading h2{
    font-size: 22px;}
.main-content .post p{margin:10px 0;}
.main-content article h3{font-size: 22px;}
.sidebar .heading{
    margin: 0 auto;
    background: #000000 url(/images/img04.gif);
}
.sidebar section{}
.sidebar .heading{color:#FFFFFF; font-size:14px; font-weight:normal; padding: 1px 5px;}
.sidebar .content{color:#F5F5F5;}
.sidebar .content a{color: rgba(173, 255, 47, 0.78)}
.sidebar .content a:hover{color: rgba(173, 255, 47, 0.78); text-decoration: underline}

article .date, .comment-date{font-size: 12px;}.comment-date{float: right;color:#8A9E92}

.comment {background: #FFFFFF;border: 1px solid #eee;padding: 20px;margin-top: 20px;}
